form.jqtransformdone label{
    display: block;
}

form.jqtransformdone div.rowElem{
	clear:both;
}

/* ------------- Selects ------------- */

.jqTransformSelectWrapper {
	position: relative;
	margin: 0;
	background:#fff;
	border:#d0d0d0 1px solid;
	height: 24px;
}

.jqTransformSelectWrapper div {
	font-size: 12px;
	padding: 5px 30px 6px 8px;
	cursor: pointer;
	line-height: 13px;
}

.jqTransformSelectWrapper  a.jqTransformSelectOpen {
	position:absolute;
	z-index: 10;
	display:block;
	right: 1px;
	top: 1px;
	width: 20px;
	height: 20px;
	background: url(../images/select-2.jpg) no-repeat 0 0;
}

.jqTransformSelectWrapper:hover  a.jqTransformSelectOpen {
	background-position: right 0;
}

.jqTransformSelectWrapper ul {
	border: #d0d0d0 1px solid;
	background:#fff;
	display: none;
	height: 150px;
	margin: 0 0px 0 0 !important; 
	padding: 0;
	overflow-y: auto;
  position: absolute;
	left: -1px;
	top: 24px !important;
	list-style: none;
	z-index: 10;
}

.jqTransformSelectWrapper ul a {
	display: block;
	font-size: 12px;
	line-height: 20px;
	padding-left: 8px;
}

.jqTransformSelectWrapper ul a.selected {
	color: #696969;
	background: #f0f0f0;
}

.jqTransformSelectWrapper ul a:hover {
	color: #696969;
	background: #e9e7e7;
}

/* ------------- Hidden - used to hide the original form elements ------------- */

.jqTransformHidden {
	display: none;
}